/* Navbar.css */

.navbar {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #fff;
    padding: 15px 7%;
    top: 0;
    position: fixed;
    z-index: 100;
    flex-wrap: wrap;
}

.logo {
    width: 100px;
    cursor: pointer;
}

.navul {
    flex: 1;
    list-style: none;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 0;
    transition: height 0.3s ease, opacity 0.3s ease;
    overflow: hidden;
    height: auto;
    max-height: 0;
    opacity: 0;
    position: relative;
}

.navul.active {
    max-height: 180px; /* Adjust based on the number of items */
    opacity: 1;
}

.navul li {
    display: inline-block;
    margin: 10px 20px;
    font-size: 18px;
    cursor: pointer;
    transition: opacity 0.3s ease;
}

.navtext {
    text-decoration: none;
    color: inherit;
}

.navtext:hover {
    color: blue;
}

.navbut {
    padding: 10px 20px;
    background-color: black;
    border-radius: 15px;
    border: none;
    cursor: pointer;
}

.navbut p {
    color: white;
    text-decoration: none;
}

.hamburger {
    display: none;
    flex-direction: column;
    cursor: pointer;
}

.hamburger div {
    width: 25px;
    height: 3px;
    background-color: black;
    margin: 5px;
}

@media screen and (max-width: 768px) {
    .navul {
        flex-direction: column;
        width: 100%;
        display: flex;
    }

    .navul.active {
        max-height: 180px; /* Adjust based on the number of items */
        opacity: 1;
    }

    .navul::after {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        height: 50px; /* Height of the fading effect */
        background: linear-gradient(to bottom, rgba(255, 255, 255, 0), #fff);
        pointer-events: none;
    }

    .navul li {
        margin: 10px 0;
    }

    .hamburger {
        display: flex;
    }
}
/* second */
.header{
    display: flex;
    justify-content: space-between;
    align-items: center;

    /*to stay on top*/
    position: fixed;
    width: 100%;
    height: 90px;
    z-index: 10;

    background: rgba((255), 255, 255, 0.25);
    backdrop-filter: blur(4px);
    -webkit-backdrop-filter: blur(4px);
}

.headerbg{
    background-color: rgba(255, 255, 255, 0.85);
    transition: 0.5s;
}


/* hamburger hide */
.hamburger{
    display: none;
}
/* //ham */

@media screen and (max-width:1040px){
    .navmenu{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background: rgba(255, 255, 255, 0.9);
        position: absolute;
        top: 0;
        left: -100%;
        z-index: -3;
        transition: 0.3s;
    }

    .navmenu.active{
        left: 0;
    }
    
    .navmenu li{
        padding: 1rem 0;
    }

    .navmenu li a{
        font-size: 2rem;
    }

    .hamburger{
        display: initial;
    }
}